import styled from "styled-components"
import { Container, Row, Input, Card } from "reactstrap"

import { Col } from "../bases/Layout"

export default class SpeakerForm extends Component {
  state = {}
  render () {
    return (
      <Container>
        <Row>
          <Col>
            <Card>
              <h2>Brown Bag</h2>
              <h3>The Fourth</h3>
              <Input />
            </Card>
          </Col>
        </Row>
      </Container>
    )
  }
}



